import { cn } from "@/lib/utils";
import { ReactNode } from "react";

interface SectionProps {
    id?: string;
    children: ReactNode;
    className?: string;
}

export default function Section({ id, children, className }: SectionProps) {
    return (
        <section
            id={id}
            className={cn(
                "py-20 px-6 md:px-12 lg:px-24 w-full max-w-7xl mx-auto",
                className
            )}
        >
            {children}
        </section>
    );
}
